@import "../../styles/vars";
@import "../../styles/base";

.contacts__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $black;
}

.contacts__content a {
    color: $black;
    transition: all 0.05s ease;
}

.contacts__content a:hover {
    color: $green;
    font-weight: 500;
}

.contacts__title {
    margin-bottom: 40px;
}

.contacts__text {
    font-size: 18px;
    text-align: center;

    margin-bottom: 50px;
}

.content-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 20px;
}

.content-list__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 1.7;
}

.item__title {
    font-size: 16px;
    font-weight: 500;
}

