@mixin stylesBtn {
    width: 100%;
    background-color: $green;
    color: white;
    transition: transform 0.2s ease-in;
}

@mixin hoverBtn {
    transform: scale(1.05);
}

@mixin activeBtn {
    position: relative;
	top: 1px;
}

@mixin mediaTablet {
    @media screen and (max-width: $tablet) {
        @content;
    }
}

@mixin mediaMobile {
    @media screen and (max-width: $mobile) {
        @content;
    }
}
